<template>
    <div class="homepage">
        <div class="home-div">
            <navigation-bar
                class="hometitle"
                pageName="社区销售"
                @onLeftClick="goBack"/>
            <div class="sale-parent-div" >
                <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
                    <div class="sale-div" >
                        <!-- sale home page -->
                        <img width="100%" style="background-color: #00D8D1; z-index: -1" :src="saleSrcImg">

                        <div class="sale-num-card">
                            <div class="sale-commerce-tip" @click="gotoOrderList(1)" :style="showReceivedEnter? 'width: 25%;' : 'width: 50%'">
                                <div class="sale-commerce-num">{{orderNum.recOrderNum}}</div>
                                <div class="sale-commerce-name">待收货单</div>
                            </div>
                            <div class="sale-commerce-tip" @click="gotoReturnList(2)" :style="showReceivedEnter? 'width: 25%;' : 'width: 50%'">
                                <div class="sale-commerce-num">{{orderNum.refundOrderNum}}</div>
                                <div class="sale-commerce-name">待退款退货单</div>
                            </div>
                            <div class="sale-commerce-tip" @click="gotoReceivedList" v-if="showReceivedEnter" :style="showReceivedEnter? 'width: 25%;' : 'width: 50%'">
                                <div class="sale-commerce-num">{{recNumDay}}/{{recNumMonth}}</div>
                                <div class="sale-commerce-name">今日/本月<br/>我的送达</div>
                            </div>
                            <div class="sale-commerce-tip" v-if="showReceivedEnter" :style="showReceivedEnter? 'width: 25%;' : 'width: 50%'">
                                <div class="sale-commerce-num">{{myCommission}}</div>
                                <div class="sale-commerce-name">我的佣金</div>
                            </div>
                        </div>

                        <div class="sale-card">
                            <div class="sale-icon-panel" @click="gotoOrderList(0)">
                                <img class="sale-icon-img" :src="iconOrderList">
                                <div class="sale-icon-name">订单列表</div>
                            </div>
                            <div class="sale-icon-panel" @click="gotoReturnList(0)">
                                <img class="sale-icon-img" :src="returnOrderList">
                                <div class="sale-icon-name">退货列表</div>
                            </div>
                        </div>
                    </div>
                </van-pull-refresh>
            </div>
        </div>
    </div>
</template>

<script>
import {
  getOrderNum,
} from "../../services/homePageService.js";
import { store, storage } from "../../utils/store";
import log from "../../utils/log.js";
import {setTags} from "../../services/loginService";
import eventBus from '../../utils/eventBus';
import NavigationBar from "../title/NavigationBar";
export default {
  name: "commerceEntrace",
  components: { NavigationBar },
  data() {
    return {
      isLoading: false,
      orderNum: { auditReturnOrderNum: 0, recOrderNum: 0, refundOrderNum: 0},
      recNumDay: 0, recNumMonth: 0, myCommission: 0,
      iconOrderList: require("../../assets/imgs/commerce/icon_order_list.png"),
      purchaseOrderList: require("../../assets/imgs/commerce/icon_purchaseorder_list.png"),
      returnOrderList: require("../../assets/imgs/commerce/icon_returnorder_list.png"),
      saleSrcImg: require("../../assets/imgs/commerce/bg_community sales.png"),
      custNews: {},
      showReceivedEnter: false,
    };
  },
  methods: {
    /**
     * 页面返回事件
     */
    goBack: function() {
        store.set("reloadCheckHouseStatus", 0);
        this.$router.back(-1);
    },
    onRefresh() {
      this.getSaleNum();
    },
    showMsg: function(msg) {
      this.$toast(msg);
    },
    getSaleNum: function() {
      //物业销售
      //加载小区订单数据
      this.getOrderNum();
      if (this.showReceivedEnter) {
        this.getReceivedNum();
        this.getMyCommission();
      }
      eventBus.$off("reloadSaleNum")
      eventBus.$on('reloadSaleNum', target => {
        this.getOrderNum();
        if (this.showReceivedEnter) {
          this.getReceivedNum();
          this.getMyCommission();
        }
      });
    },
    getOrderNum: function() {
      if (!this.custNews.AREA_CODE) {
        log.i("area code empty, do not request order number.");
        this.isLoading = false;
        return;
      }
      var data = {
        recAreaCode: this.custNews.AREA_CODE
      };
      getOrderNum(data)
        .then(
          res => {
            log.l("the order num request result " + res);
            if (res.resultCode == "0") {
              this.orderNum = res.data;
              this.isLoading = false;
              log.i(this.orderNum)
            } else {
              res.msg
                ? this.showMsg(res.msg)
                : this.showMsg("网络异常，请稍后重试！");
                this.isLoading = false;
            }
          },
          error => {
            log.e("get order number " + JSON.stringify(error));
            this.isLoading = false;
          }
        )
        .catch(error => {
          log.e("get order exception " + JSON.stringify(error));
          this.isLoading = false;
        });
    },
    getReceivedNum: function() {
      // todo: debug with interface.
      this.recNumDay = 22;
      this.recNumMonth = 222;
    },
    getMyCommission: function() {
      // todo: debug with interface.
      this.myCommission = 222;
    },
    gotoOrderList: function(status) {
      // 跳转订单列表
      if (
        this.custNews.AREA_CODE == "" ||
        this.custNews.AREA_CODE == undefined
      ) {
        this.showMsg("账号权限未分配，请联系管理员。");
        return;
      }

      // go to commerceOrderList
      this.$router.push({
        name: "commerceOrderList",
        params: {
          orderInfo: status
        }
      });
    },
    gotoReturnList: function(status) {
      if (
        this.custNews.AREA_CODE == "" ||
        this.custNews.AREA_CODE == undefined
      ) {
        this.showMsg("账号权限未分配，请联系管理员。");
        return;
      }
      // goto commerceReturnList
      // 跳转退货列表
      this.$router.push({
        name: "commerceReturnList",
        params: {
          orderInfo: status
        }
      });
    },
    gotoPurchaseList: function(status) {
      if (
        this.custNews.AREA_CODE == "" ||
        this.custNews.AREA_CODE == undefined
      ) {
        this.showMsg("账号权限未分配，请联系管理员。");
        return;
      }
      // goto commercePurchaseList
      // 跳转采购列表
      this.$router.push({
        name: "commercePurchaseList",
        params: {
          load: true
        }
      });
    },
    gotoReceivedList: function() {
      if (
        this.custNews.AREA_CODE == "" ||
        this.custNews.AREA_CODE == undefined
      ) {
        this.showMsg("账号权限未分配，请联系管理员。");
        return;
      }
      // switch to received list
      this.$router.push({
        name: "commerceReceivedList",
        params: {
          load: true
        }
      });
    },
  },
  created() {
    /*if (this.$route.params.userInfo) {
      this.custNews = this.$route.params.userInfo;
    }*/
    if (!this.custNews) {
      // update by store or storage
      let userInfo = (store.get("custNews") && store.get("custNews").AREA_CODE) ?
              store.get("custNews") : storage.getStorage("custNews");
      this.custNews = userInfo;
    }
    if (storage.getStorage("custNews") && storage.getStorage("custNews").AREA_CODE
       && this.custNews && this.custNews.AREA_CODE != storage.getStorage("custNews").AREA_CODE) {
       this.custNews = storage.getStorage("custNews");
    }
    if (undefined !== this.custNews) {
      this.getSaleNum();
    } else {
      log.e("The user info empty, can not fetch village list!!!");
    }
  },
  beforeDestroy () {
    eventBus.$off("reloadSaleNum")
  },
};
</script>

